body {
  margin: 0;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

:root {
  --secondary-color: #00acdc;
}

.header {
  text-transform: uppercase;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-weight: 500;
  display: flex;
}

.header__link {
  width: 40px;
  height: 40px;
}

.nav {
  justify-content: center;
  align-items: center;
  display: flex;
}

.nav__list {
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  align-items: center;
  margin-left: 20px;
  display: flex;
}

.nav__item:first-of-type {
  margin-left: 0;
}

.nav__link {
  color: #000;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
}

.is-active {
  color: var(--secondary-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--secondary-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.nav__link:hover {
  color: var(--secondary-color);
  text-decoration: none;
}
